.modal-pop{
  position: fixed;
  z-index: 7;
  background-color: rgba(0,0,0,0.6);
  left: 0;
  top: 0;
  overflow: hidden;
  height: 100%;
  width: 100%;
  display: grid;
  justify-content: center;
  align-items: center;
}


.modal-background{
  position: fixed;
  z-index: -1;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
}